<template>
  <div class="login">
    <div class="middle-wrapper">
      <div class="title-wrapper">
        <div class="title">乾 图 后台管理系统</div>
      </div>
      <div class="login-form">
        <el-form :model="form" :rules="rules" ref="loginForm">
          <el-form-item prop="username">
            <el-input placeholder="请输入账号(试用账号是admin)" v-model="form.username">
              <i class="el-input__icon el-icon-adm-user" slot="prefix" style="font-size: 18px;"></i>
            </el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input :type="passwordType" placeholder="请输入密码(试用密码是123456)" v-model="form.password">
              <i class="el-input__icon el-icon-adm-password" slot="prefix" style="font-size: 18px;"></i>
              <i
                @click="_togglePasswordType"
                class="el-input__icon el-icon-view"
                slot="suffix"
                style="cursor: pointer;"
              ></i>
            </el-input>
          </el-form-item>
          <el-form-item prop="yanzhengma">
            <div class="check-code-wrapper">
              <div class="yanzhengma-wrapper">
                <el-input
                  @keyup.enter.native="login('loginForm')"
                  placeholder="请输入验证码"
                  v-model="form.yanzhengma"
                >
                  <i
                    class="el-input__icon el-icon-adm-vertification"
                    slot="prefix"
                    style="font-size: 18px;"
                  ></i>
                </el-input>
              </div>
              <div class="validate-code-wrapper">
                <validate-code @change="_setCheckCode" ref="validate-code"></validate-code>
              </div>
            </div>
          </el-form-item>
          <el-form-item style="margin-bottom: 0;">
            <el-col :offset="14" :span="10">
              <el-form-item>
                <el-button
                  @click="loginHandle('loginForm')"
                  class="login-btn"
                  style="width: 100%;"
                  type="primary"
                >登陆系统</el-button>
              </el-form-item>
            </el-col>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
import validateCode from 'src/components/ValidateCode/index';

export default {
  created () {
  },
  data () {
    // var checkYanzhengma = (rule, value, callback) => {
    //   value = value.toUpperCase();
    //   if (value === '') {
    //     callback(new Error('请输入验证码'));
    //   } else if (value !== this.checkCode) {
    //     callback(new Error('验证码错误'));
    //     this.$refs['validate-code'].draw();
    //   } else {
    //     callback();
    //   }
    // };
    return {
      passwordType: 'password',
      checkCode: '',
      form: {
        username: '',
        password: '',
        yanzhengma: ''
      },
      rules: {
        username: [
          { required: true, message: '账号不能为空', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '密码不能为空', trigger: 'blur' }
        ]
        // yanzhengma: [
        //   { validator: checkYanzhengma, trigger: 'blur' }
        // ]
      }
    };
  },
  methods: {
    _setCheckCode (value) {
      this.checkCode = value;
    },
    _togglePasswordType () {
      if (this.passwordType === 'password') {
        this.passwordType = 'text';
      } else {
        this.passwordType = 'password';
      }
    },
    loginHandle (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.login();
        }
      });
    },
    login () {
      var _this = this;
      this.postRequest('/login', { username: this.form.username, password: this.form.password }).then(res => {
        if (res.data.code === 0) {
          _this.$store.commit('SET_ID', res.data.id);
          _this.$store.commit('SET_TOKEN', res.data.token.token);
          _this.$router.replace('home');
        } else {
          _this.$message({
            showClose: true,
            message: '用户名或密码错误',
            type: 'error'
          });
        }
      }).catch(function () {
        _this.$message({
          showClose: true,
          message: '网络异常',
          type: 'error'
        });
      })
    }
  },
  components: {
    validateCode
  }
};
</script>
<style lang="scss" scoped>
.login {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgb(32, 160, 255);

  .middle-wrapper {
    position: fixed;
    width: 100%;
    margin: 0 auto;
    top: 50%;
    transform: translateY(-60%);

    .title-wrapper {
      margin-top: 50px;
      margin-bottom: 30px;
      text-align: center;
      font-size: 30px;
      font-weight: bold;
      color: #ffffff;
    }
    .login-form {
      position: relative;
      margin: 0 auto;
      width: 520px;
      padding: 30px 50px;
      box-sizing: border-box;
      border-radius: 5px;
      background-color: #ffffff;

      .el-row {
        margin-bottom: 20px;

        &:last-child {
          margin-bottom: 0;
        }

        .login-btn {
          width: 100%;
        }
      }

      .check-code-wrapper {
        display: flex;
        justify-content: space-between;
        height: 40px;

        .yanzhengma-wrapper {
          flex: 0 1 auto;
        }

        .validate-code-wrapper {
          flex: 0 0 160px;
        }
      }
    }
  }
}
</style>



